import { Component, Host, h } from '@stencil/core';
import { onChange } from '../../store/store';

@Component({
  tag: 'chat-template',
  styleUrl: './chat-template.scss',
  shadow: true,
})
export class ChatTemplate {
  private chatElement!: HTMLElement; // define a variable for html element
  connectedCallback() {
    onChange('messages', () => {
      setTimeout(()=>this.chatElement.scrollTo(0,this.chatElement.scrollHeight),10)
      
      console.log(this.chatElement.scrollHeight)
    });
  }  
 
  render() {
    
    return (
      <Host>
        <div class="dav-container">
          <div class="container">
            <div class="head">
              <slot name="head">
                <chat-header></chat-header>
              </slot>
            </div>
            <div class="content" ref={(el) => this.chatElement= el as HTMLElement}>
              <slot name="content">
                <chat-component></chat-component>
              </slot>
            </div>
            <div class="footer">
              <slot name="footer">
                <chat-footer></chat-footer>
              </slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }

}
