
@import '../../global/box-container.scss';
.container {
  display: flex;
  width: 100%;
  height: 100vh;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
}

.head {
  
  height: fit-content;
  width: 100%;
  position: fixed;
  max-width: 538px;
  z-index: 1;
}

.content {
  position: relative;
  min-width: 100%;
  padding: 0;
  overflow-y: auto;
  max-width: 538px;
  margin-top: 48px;
  //display: flex;
  //flex-direction: column;
  //justify-content: space-between;
  
}

.footer {
  background-color: transparent;
  padding: 0;
  position: relative;
  bottom: 16px;
  width: 100%;
  //padding-bottom: 16px;
  max-width: 538px;
  margin-top: 16px;
}
